<template>
  <div class="like">
    <div class="topBgcolor"></div>

    <!-- 导航条 -->
    <van-nav-bar
      title="我的收藏"
      left-text="返回"
      left-arrow
      :fixed='true'
      @click-left="back"
    />

    <!-- 头部背景图 -->
    <div class="my-bg">
      <div class="bg auto-img">
      </div>
    </div>

    <!-- 收藏列表 -->
    <div class="like-box" v-if="likeData.length > 0">

      <div class="product-box clearfix">
        <div
          class="product-item fl"
          :class="[index % 2 == 0 ? 'p-left' : 'p-right']"
          v-for="(item, index) in likeData"
          :key="index"
        >
          <div
            class="p-img"
            @click="viewProductInfo(item.pid)"
          >
            <img
              class="auto-img"
              :src="item.smallImg"
              alt=""
            >
          </div>
          <div class="pro-name one-text">{{item.name}}</div>
          <div class="clearFix">
            <!-- <div class="en-pro-name onr-text fl">heitangbobonaicha</div> -->
            <div class="pro-price fl">{{item.price}}</div>
            <div class="pro-remove fr">
              <van-icon
                name="delete"
                size="20"
                @click="removeLike(item.pid,index)"
              />
            </div>
          </div>
        </div>
      </div>

    </div>

    <!-- 无商品的列表背景 -->
    <div
      class="main-box-null"
      v-else
    >
      <van-empty
        class="custom-image"
        image="https://img.yzcdn.cn/vant/custom-empty-image.png"
        description="没有收藏宝贝，快去挑选吧"
      >
        <van-button
          round
          color="#0b34ba"
          class="bottom-button"
          @click="goMenu"
        >
          马上去逛逛
        </van-button>
      </van-empty>
    </div>

  </div>
</template>

<script>
import "../assets/less/like.css";
import { utils } from "../assets/js/utils";

export default {
  data() {
    return {
      // 我的收藏商品数据
      likeData: [],
    };
  },

  created() {
    this.getAllLikeData();
  },

  methods: {
    // 跳转上一步
    back() {
      this.$router.go(-1);
    },

    // 回到菜单页面
    goMenu() {
      this.$router.push({ name: "Menu" });
    },

    // 获取我的收藏商品数据
    getAllLikeData() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$toast('请先登录')
        return this.$router.push({name:'Login'});
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllLike",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          

          if (result.data.code == 2000) {
            this.likeData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 查看商品详情
    viewProductInfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },

    // 删除收藏数据
    removeLike(pid, index) {
      // pid  商品id
      // index  下标
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/notlike",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        data,
      })
        .then((result) => {
          this.$toast.clear();
          

          if (result.data.code == 700) {
            return this.$router.push({ name: "Login" });
          }

          if (result.data.code == 900) {
            this.$toast("删除收藏商品成功");
            this.likeData.splice(index,1);
          } else {
            this.$toast("删除收藏商品失败");
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>